import * as React from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import produce from "immer";


import { useSelector, useDispatch } from 'react-redux';
import {
    seedArray,
    setStoreObject,

    selectCaseDndItemsArray,
    selectCaseDndItems2Array,
    // selectSelectedNote
} from "../features/case/caseSlice";

//import Button from "react-bootstrap/Button";
//import pushKeyFirebaseNode from "../features/firebase/pushKeyFirebaseNode";



const dragReducer = produce((draft, action) => {
    console.log(action);
    switch (action.type) {
        case "MOVE": {
            draft[action.from] = draft[action.from] || [];
            draft[action.to] = draft[action.to] || [];
            const [removed] = draft[action.from].splice(action.fromIndex, 1);
            draft[action.to].splice(action.toIndex, 0, removed);
        }
    }
});


function DragAndDrop() {
    // const key = useSelector(selectCaseKey);

    // React.useEffect(() => {
    //     console.log(key);
    // }, [key])

    return <DndLayout />
}

export default DragAndDrop


function DndLayout() {
    const dispatch = useDispatch();
    const caseDndItemsArray = useSelector(selectCaseDndItemsArray);
    const caseDndItems2Array = useSelector(selectCaseDndItems2Array);

    //  const caseArray = useSelector(selectCaseDNDArrayInitial);
    const [state, localDispatch] = React.useReducer(dragReducer,
        {
            items: caseDndItemsArray,
            items2: caseDndItems2Array,
            itemsTitle: (Array.isArray(caseDndItemsArray) && caseDndItemsArray.length > 0) ? caseDndItemsArray[0].dndcolumn : "...",
            items2Title: (Array.isArray(caseDndItems2Array) && caseDndItems2Array.length > 0) ? caseDndItems2Array[0].dndcolumn : "Корзина"
        }
    );





    const onDragEnd = React.useCallback((result) => {

        if (result.reason === "DROP") {
            if (!result.destination) {
                return;
            }


            const nextState = produce(state, draft => {
                draft[result.source.droppableId] = draft[result.source.droppableId] || [];
                draft[result.destination.droppableId] = draft[result.destination.droppableId] || [];
                const [removed] = draft[result.source.droppableId].splice(result.source.index, 1);
                draft[result.destination.droppableId].splice(result.destination.index, 0, removed);

            });

            console.log(...nextState.items
                .map(item => { return { ...item, dndcolumn: state.itemsTitle } })
                .map(item => {
                    let { dndcontent, ...rest } = item;
                    return rest
                }));

            console.log(...nextState.items2
                .map(item => { return { ...item, dndcolumn: state.items2Title } })
                .map(item => {
                    let { dndcontent, ...rest } = item;
                    return rest
                }));


            dispatch(seedArray({
                arrayName: "caseDNDArrayFinal",
                arrayItems: [
                    ...nextState.items
                        .map(item => { return { ...item, dndcolumn: state.itemsTitle } })
                        .map(item => {
                            let { dndcontent, ...rest } = item;
                            return rest
                        }),
                    ...nextState.items2
                        .map(item => { return { ...item, dndcolumn: state.items2Title } })
                        .map(item => {
                            let { dndcontent, ...rest } = item;
                            return rest
                        })
                ]
            }));
            //  dispatch(setStoreObject({
            //      key: "key",
            //      value: pushKeyFirebaseNode("/usersCraft/")
            //  }));

            localDispatch({
                type: "MOVE",
                from: result.source.droppableId,
                to: result.destination.droppableId,
                fromIndex: result.source.index,
                toIndex: result.destination.index,
            });
        }
    });




    const grid = 8;

    function getItemStyle(isDragging, draggableStyle) {
        return {
            userSelect: "none",
            padding: grid * 2,
            margin: `0 0 ${grid}px 0`,
            color: isDragging ? "white" : "grey",
            background: isDragging ? "#185abd" : "white", //"lightgreen"  "grey"  "#6c757d"
            ...draggableStyle
        }
    }

    function getListStyle(isDraggingOver) {
        return {
            background: isDraggingOver ? "#107a40" : "#f8f9fa", //"lightblue"  "lightgrey"
            padding: grid,
            width: 500
        }
    };

    const isBrowser = typeof window !== "undefined";

    // console.log(state);

    if (!!isBrowser) {
        return <div className="container p-5">

            <div style={{ display: "flex" }}>
                <DragDropContext onDragEnd={onDragEnd}>

                    <Droppable droppableId="items" type="PERSON">


                        {(provided, snapshot) => {
                            return (
                                <div
                                    ref={provided.innerRef}
                                    {...provided.droppableProps}
                                    style={getListStyle(snapshot.isDraggingOver)}
                                >
                                    <small>{state.itemsTitle}</small>
                                    {Array.isArray(state?.items) && state.items.map((item, index) => {
                                        return (
                                            <Draggable
                                                key={item.id}
                                                draggableId={item.id}
                                                index={index}
                                            >
                                                {(provided, snapshot) => {
                                                    return (
                                                        <div
                                                            ref={provided.innerRef}
                                                            {...provided.draggableProps}
                                                            {...provided.dragHandleProps}
                                                            style={getItemStyle(
                                                                snapshot.isDragging,
                                                                provided.draggableProps.style
                                                            )}
                                                            className="border border-secondary"
                                                        >

                                                            <div
                                                                style={{
                                                                    display: "flex",
                                                                    justifyContent: "space-around"
                                                                }}
                                                            >
                                                                {item.dndcontent}
                                                                " "
                                                                <button className="btn btn-sm"
                                                                    onClick={() => dispatch(setStoreObject({ key: "selectedNote", value: item }))}

                                                                >
                                                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-binoculars" viewBox="0 0 16 16">
                                                                        <path d="M3 2.5A1.5 1.5 0 0 1 4.5 1h1A1.5 1.5 0 0 1 7 2.5V5h2V2.5A1.5 1.5 0 0 1 10.5 1h1A1.5 1.5 0 0 1 13 2.5v2.382a.5.5 0 0 0 .276.447l.895.447A1.5 1.5 0 0 1 15 7.118V14.5a1.5 1.5 0 0 1-1.5 1.5h-3A1.5 1.5 0 0 1 9 14.5v-3a.5.5 0 0 1 .146-.354l.854-.853V9.5a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5v.793l.854.853A.5.5 0 0 1 7 11.5v3A1.5 1.5 0 0 1 5.5 16h-3A1.5 1.5 0 0 1 1 14.5V7.118a1.5 1.5 0 0 1 .83-1.342l.894-.447A.5.5 0 0 0 3 4.882V2.5zM4.5 2a.5.5 0 0 0-.5.5V3h2v-.5a.5.5 0 0 0-.5-.5h-1zM6 4H4v.882a1.5 1.5 0 0 1-.83 1.342l-.894.447A.5.5 0 0 0 2 7.118V13h4v-1.293l-.854-.853A.5.5 0 0 1 5 10.5v-1A1.5 1.5 0 0 1 6.5 8h3A1.5 1.5 0 0 1 11 9.5v1a.5.5 0 0 1-.146.354l-.854.853V13h4V7.118a.5.5 0 0 0-.276-.447l-.895-.447A1.5 1.5 0 0 1 12 4.882V4h-2v1.5a.5.5 0 0 1-.5.5h-3a.5.5 0 0 1-.5-.5V4zm4-1h2v-.5a.5.5 0 0 0-.5-.5h-1a.5.5 0 0 0-.5.5V3zm4 11h-4v.5a.5.5 0 0 0 .5.5h3a.5.5 0 0 0 .5-.5V14zm-8 0H2v.5a.5.5 0 0 0 .5.5h3a.5.5 0 0 0 .5-.5V14z" />
                                                                    </svg>
                                                                </button>

                                                            </div>



                                                        </div>
                                                    );
                                                }}
                                            </Draggable>
                                        );
                                    })}
                                    {provided.placeholder}
                                </div>
                            );
                        }}
                    </Droppable>

                    <Droppable droppableId="items2" type="PERSON">
                        {(provided, snapshot) => {
                            return (
                                <div
                                    ref={provided.innerRef}
                                    {...provided.droppableProps}
                                    style={getListStyle(snapshot.isDraggingOver)}

                                >
                                    <small>{state.items2Title}</small>
                                    {Array.isArray(state?.items2) && state.items2.map((item, index) => {
                                        return (
                                            <Draggable
                                                key={item.id}
                                                draggableId={item.id}
                                                index={index}
                                            >
                                                {(provided, snapshot) => {
                                                    return <div
                                                        ref={provided.innerRef}
                                                        {...provided.draggableProps}
                                                        {...provided.dragHandleProps}
                                                        style={getItemStyle(
                                                            snapshot.isDragging,
                                                            provided.draggableProps.style
                                                        )}
                                                        className="border border-secondary"
                                                    >

                                                        <div
                                                            style={{
                                                                display: "flex",
                                                                justifyContent: "space-around"
                                                            }}
                                                        >
                                                            {item.dndcontent}
                                                            " "
                                                            <button className="btn btn-sm"
                                                                onClick={() => dispatch(setStoreObject({ key: "selectedNote", value: item }))}

                                                            >
                                                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-binoculars" viewBox="0 0 16 16">
                                                                    <path d="M3 2.5A1.5 1.5 0 0 1 4.5 1h1A1.5 1.5 0 0 1 7 2.5V5h2V2.5A1.5 1.5 0 0 1 10.5 1h1A1.5 1.5 0 0 1 13 2.5v2.382a.5.5 0 0 0 .276.447l.895.447A1.5 1.5 0 0 1 15 7.118V14.5a1.5 1.5 0 0 1-1.5 1.5h-3A1.5 1.5 0 0 1 9 14.5v-3a.5.5 0 0 1 .146-.354l.854-.853V9.5a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5v.793l.854.853A.5.5 0 0 1 7 11.5v3A1.5 1.5 0 0 1 5.5 16h-3A1.5 1.5 0 0 1 1 14.5V7.118a1.5 1.5 0 0 1 .83-1.342l.894-.447A.5.5 0 0 0 3 4.882V2.5zM4.5 2a.5.5 0 0 0-.5.5V3h2v-.5a.5.5 0 0 0-.5-.5h-1zM6 4H4v.882a1.5 1.5 0 0 1-.83 1.342l-.894.447A.5.5 0 0 0 2 7.118V13h4v-1.293l-.854-.853A.5.5 0 0 1 5 10.5v-1A1.5 1.5 0 0 1 6.5 8h3A1.5 1.5 0 0 1 11 9.5v1a.5.5 0 0 1-.146.354l-.854.853V13h4V7.118a.5.5 0 0 0-.276-.447l-.895-.447A1.5 1.5 0 0 1 12 4.882V4h-2v1.5a.5.5 0 0 1-.5.5h-3a.5.5 0 0 1-.5-.5V4zm4-1h2v-.5a.5.5 0 0 0-.5-.5h-1a.5.5 0 0 0-.5.5V3zm4 11h-4v.5a.5.5 0 0 0 .5.5h3a.5.5 0 0 0 .5-.5V14zm-8 0H2v.5a.5.5 0 0 0 .5.5h3a.5.5 0 0 0 .5-.5V14z" />
                                                                </svg>
                                                            </button>

                                                        </div>
                                                    </div>
                                                }}
                                            </Draggable>
                                        );
                                    })}
                                    {provided.placeholder}
                                </div>
                            );
                        }}
                    </Droppable>

                </DragDropContext>
            </div >
        </div>
    }

    return null
}



